import React from 'react'
import CitizenApplyApiList from '../../../Components/CitizenApplyApiList'
import CustomErrorBoundary from '../../../Components/Common/CustomErrorBoundary'
import TransactionHistoryFactory from '../TransactionHistoryFactory'

function PropertyAllTransactionsEntry() {

  // LIST OF API'S
  const { api_getTranscationHistory } = CitizenApplyApiList()

  const transactionRules = {
    api: {
      // 1 - API TO FETCH TRANSACTION HISTORY LIST
      api_getTranscationHistory: { method: 'get', url: api_getTranscationHistory },
    },
    transactionInfo: {
      title: 'Property Payment History',
    },

    tableColumns: [
      {
        Header: "#",
        Cell: ({ row }) => <div className="pr-2">{row.index + 1}</div>,
      },
      {
        Header: "Application No.",
        accessor: "saf_no",
      },
      {
        Header: "Transaction No.",
        accessor: "tran_no",
      },
      {
        Header: "Payment Mode",
        accessor: "payment_mode",
      },
      {
        Header: "Transaction Date",
        accessor: "created_at",
      },
      {
        Header: "Amount",
        accessor: "amount",
      }
    ],
    filters: {
      // PASS true IF YOU WANT TO SEARCH TRANSACTIONS VIA APPLICATION NO ALSO
      applicationNo: true
    }
  }
  return (
    <CustomErrorBoundary errorMsg="Bug in TransactionHistoryFactory" >
      <TransactionHistoryFactory transactionRules={transactionRules} />
    </CustomErrorBoundary>
  )
}

export default PropertyAllTransactionsEntry